import myScrollX from '@/components/my-scrollX/my-scrollX.vue'
import MyList from '@/components/my-list/my-list.vue'
export default {
	components: {
		myScrollX,
		MyList,
	},
	data() {
		return {
			scrollTopList:[],
			currentTopTab: 0,
			beforeColor: '#666666',
			afterColor: '#2E3B67',
			placeholderText: '请输入搜索内容',
			changeValue: '',
			trainInfo:{},
			collectsList:[],
			page:1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'loading',
			code:'',
			organizeId: '',//分组id
		};
	},
	onPullDownRefresh() {
		this.page = 1;
		this.trainInfo = {};
		this.collectsList = []
		this.changeValue = ''
		uni.showLoading({
			title: '加载中'
		});
		this.getTrainList()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	onReachBottom(){
		if (this.code != '-116') {
			this.page = this.page + 1;
			this.getTrainList();
		}
	},
	onShow(){
		this.page = 1;
		this.trainInfo = {};
		this.collectsList = []
		this.currentTopTab = 0
		this.changeValue = ''
		this.organizeId = ''
		this.getTrainList()
	},
	methods:{
		// 获取首页数据列表
		getTrainList(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get(`/appCollectsController.do?getTrainList&memberId=${memberId}&page=${this.page}&type=1&collectsName=${this.changeValue}&organizeId=${this.organizeId}`)
				.then(res => {
					this.code = res.code
					this.trainInfo = res.data
					let scrollTopList = []
					res.data.organizeList.forEach(item => {
						scrollTopList.push({
							id: item.id,
							groupName:item.organizeName
						})
					})
					scrollTopList.unshift({
						id: '',
						groupName:'全部'
					})
					this.scrollTopList = scrollTopList
					this.status = 'noMore'
					if(res.code == 0){
						this.collectsList =  [...this.collectsList, ...res.data.collectsList].map(item => {
							return {
								...item,
								studyDate: item.studyDate && item.studyDate.substring(0,10)
							}
						})
					}else if(res.code == '-118' || res.code == '-116'){
						this.status = 'noMore'
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '您尚未登录，正在跳往登录页面。。。',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url:'/pages/loginAll/login'
					})
				}, 1000)
			}
		},
		gotoListDetail(item){
			uni.navigateTo({
				url: `/pages/train/imageMemory/numEleEntry?id=${item.id}`
			})
		},
		// 收藏
		clickAttention(item, index){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			let collectsId = item.id
			this.$Request.get(`/appAttentionController.do?takeCollectsAttention&memberId=${memberId}&collectsId=${collectsId}&organizeId=${this.organizeId}`)
			.then(res => {
				if(res.code == 0){
					this.collectsList[index].attentionNum = item.attentionType == 1 ? item.attentionNum - 1 : item.attentionNum + 1
					this.collectsList[index].attentionType = item.attentionType == 1 ? 0 : 1
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		gotoUrl(url){
			uni.navigateTo({
				url: url
			})
		},
		tabChange(item, index){
			this.page = 1;
			this.trainInfo = {};
			this.collectsList = []
			this.currentTopTab = index
			this.organizeId = item.id
			this.getTrainList()
		},
		bindNameInput(e){
			this.changeValue = e.target.value
			this.trainInfo = {};
			this.collectsList = []
			this.page = 1;
			this.getTrainList()
		},
		clickSearch(){
			this.trainInfo = {};
			this.collectsList = []
			this.page = 1;
			this.getTrainList()
		},
	}
}
